export function looksLikeUuid(id: string): boolean {
  const s = String(id || '')
  return s.includes('-') && s.length >= 32
}

export function normalizeProjectId(raw: string): string {
  const val = String(raw || '')
  if (looksLikeUuid(val)) return val

  try {
    const hinted = (globalThis as any)?.__activeProjectId as string | undefined
    if (hinted && looksLikeUuid(hinted)) return hinted
  } catch {}

  try {
    const ls: Storage | undefined = (globalThis as any)?.localStorage
    if (ls) {
      const stored = ls.getItem('activeProjectId') || ls.getItem('currentProjectId')
      if (stored && looksLikeUuid(stored)) return stored
    }
  } catch {}

  return val
}

export function persistActiveProjectId(id: string): void {
  try {
    if (!id) return
    const ls: Storage | undefined = (globalThis as any)?.localStorage
    if (ls) ls.setItem('activeProjectId', id)
  } catch {}
}

